<template>
    <div class="product-page container">
        <div class="row">
            <div class="side-bar col-lg-3 col-12">
                <div class="categories-list">
                    <h4>Sản phẩm</h4>
                    <span class="line">
                    </span>
                    <ul>
                        <li v-for="(category, index) in categories.slice(1,5)" :key="index">
                            <router-link :to="{ name:'sanphamRouter', params: { categoryname: category}}">{{category}}</router-link>
                        </li>
                    </ul>
                </div>
                <div class="filter-box">
                    <h4>Lọc theo giá</h4>
                    <span class="line"></span>
                    <!-- <div>
                        <p>Sản phẩm:</p>
                        <div>
                            <div>
                                <input type="checkbox" id="all" class="me-2" name="filter-checkbox" v-model="allCheck">
                                <label for="all">Tất cả</label>
                            </div>
                            <div>
                                <input type="checkbox" id="dog" class="me-2" name="filter-checkbox" v-model="dogCheck">
                                <label for="dog">Chó cảnh</label>
                            </div>
                            <div>
                                <input type="checkbox" id="cat" class="me-2" name="filter-checkbox" v-model="catCheck">
                                <label for="cat">Mèo cảnh</label>
                            </div>
                            <div>
                                <input type="checkbox" id="food" class="me-2" name="filter-checkbox"
                                    v-model="foodCheck">
                                <label for="food">Đồ ăn</label>
                            </div>
                            <div>
                                <input type="checkbox" id="accessory" class="me-2" name="filter-checkbox"
                                    v-model="accessoryCheck">
                                <label for="accessory">Phụ kiện</label>
                            </div>
                        </div>
                    </div> -->
                    <div class="mt-4">
                        <input type="range" class="filter-bar w-100" min="1" :max="filterRange" v-model="filterValue">
                        <p>Mức giá: Dưới <span class="fw-bold">{{filterValue}}</span> triệu</p>
                        <button class="filter-btn" @click="filterProduct2">Lọc sản phẩm</button>
                    </div>
                </div>
            </div>
            <div class="col-lg-9 col-12">
                <div class="row">
                    <div class="col-lg-4 col-6" v-for="(product, index) in products" :key="index">
                        <Product :product="product"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Product from './Product.vue';
import { mapGetters } from 'vuex';

export default {
    name: "Product-page",
    props: ['categoryname'],
    data() {
        return {
            categories: [
                'Tất cả',
                'Chó cảnh',
                'Mèo cảnh',
                'Đồ ăn',
                'Phụ kiện'
            ],
            products: [],
            filterRange: 15,
            filterValue: 2,
        }
    },
    components: { Product },
    computed: {
        ...mapGetters(['data'])
    },
    mounted() {
        this.products = this.data.products.filter( el => el.categoryName == this.categoryname)
    },
    watch: {
        categoryname() {
        this.products = this.data.products.filter( el => el.categoryName == this.categoryname)
        }
    },
    methods: {
        filterProduct2: function () {
            if (this.allCheck || this.dogCheck || this.catCheck || this.foodCheck || this.accessoryCheck) {
                this.products = []
                if (this.allCheck) {
                    this.data.products.forEach(product => {
                        if (product.price < this.filterValue * 1000000)
                            this.products.push(product)
                    })
                }
                if (this.dogCheck) {
                    this.data.products.forEach(product => {
                        if (product.categoryName == 'Chó cảnh' && product.price < this.filterValue * 1000000)
                            this.products.push(product)
                    })
                }
                if (this.catCheck) {
                    this.data.products.forEach(product => {
                        if (product.categoryName == 'Mèo cảnh' && product.price < this.filterValue * 1000000)
                            this.products.push(product)
                    })
                }
                if (this.foodCheck) {
                    this.data.products.forEach(product => {
                        if (product.categoryName == 'Đồ ăn' && product.price < this.filterValue * 1000000)
                            this.products.push(product)
                    })
                }
                if (this.accessoryCheck) {
                    this.data.products.forEach(product => {
                        if (product.categoryName == 'Phụ kiện' && product.price < this.filterValue * 1000000)
                            this.products.push(product)
                    })
                }
            }
        }
    }
}
</script>

<style>
.product-page {
        min-height: 100vh;
        padding-top: 100px;
    }
.side-bar h4 {
    text-transform: uppercase;
}
.line {
margin: 15px 0;
}
.side-bar ul {
    padding: 10px;
    border: 1px solid rgb(181, 181, 181);
}

.side-bar ul li {
    border-bottom: 1px solid rgb(181, 181, 181);
}

.side-bar ul li:last-child {
    border: none;
}

.side-bar ul li a {
    line-height: 50px;
}

.filter-box {
    margin-top: 40px;
}

.filter-btn {
    padding: 0 20px;
    line-height: 40px;
    color: #fff;
    font-weight: bold;
    border: none;
    background-color: rgb(48, 181, 178);
    border-radius: 5px;
}

.filter-btn:hover {
    box-shadow: 0 0 10px 0 rgb(48, 181, 178);
}

.filter-bar {
    -webkit-appearance: none;
    /* Override default CSS styles */
    appearance: none;
    width: 100%;
    /* Full-width */
    height: 10px;
    /* Specified height */
    background: #d3d3d3;
    /* Grey background */
    outline: none;
    /* Remove outline */
    opacity: 0.7;
    /* Set transparency (for mouse-over effects on hover) */
    -webkit-transition: .2s;
    /* 0.2 seconds transition on hover */
    transition: opacity .2s;
    border-radius: 5px;
}

.filter-bar:hover {
    opacity: 1;
}

.filter-bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #04AA6D;
    cursor: pointer;
}
</style>