<template>
    <div class="cart container">
        <div class="row">
            <div class="col-lg-7 col-12">
                <ul>
                    <li class="row cart-th">
                        <div class="col-6">Sản phẩm</div>
                        <div class="col-2">Giá</div>
                        <div class="col-2">Số lượng</div>
                        <div class="col-2">Tổng cộng</div>
                    </li>
                    <li class="text-center my-4" v-show="listShow">Giỏ hàng hiện không có sản phẩm</li>
                    <li class="row cart-item" v-for="(item, index) in yourCart" :key="index">
                        <div class="col-6 cart-item__img">
                            <button @click="clearItem(index)">x</button>
                            <div>
                                <img :src="require(`../assets/products/${item.imgUrl}`)" alt="">
                            </div>
                            <p>{{item.name}}</p>
                        </div>
                        <div class="col-2 cart-item__price">
                            {{new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND'}).format(item.price)}}
                        </div>
                        <div class="col-2 cart-item__count"><button @click="count(1,index)">+</button>
                            <p>{{item.count}}</p><button @click="count(0,index)">-</button>
                        </div>
                        <div class="col-2 cart-item__total">
                            {{new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND'}).format(item.price * item.count)}}
                        </div>
                    </li>

                </ul>
                <div class="col-12">
                    <button class="back" @click="back">Tiếp tục xem sản phẩm</button>
                    <button class="cart-update" @click="updateCart">Cập nhật giỏ hàng</button>
                </div>
            </div>
            <div class="col-lg-5 col-12">
                <h6 class="">Tổng số lượng</h6>
                <div class="d-flex justify-content-between">
                    <p>Tổng cộng</p>
                    <span>2.302.303 đ</span>
                </div>
                <div class="d-flex justify-content-between">
                    <p>Tổng cộng</p>
                    <span>2.302.303 đ</span>
                </div>
                <div class="d-flex justify-content-between">
                    <p>Tổng cộng</p>
                    <span>2.302.303 đ</span>
                </div>
                <div>
                    <button class="cart-pay w-100">
                        Thanh toán
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: 'Cart-component',
    computed: {
        ...mapGetters(['cart'])
    },
    methods: {
        checkCart() {
            if (this.yourCart.length == 0) {
                this.listShow = true
            } else {
                this.listShow = false
            }
        },
        count(number, index) {
            if (number == 1) {
                this.yourCart[index].count++
                sessionStorage.setItem('yourCart', JSON.stringify(this.yourCart))
                window.location.href = "/giohang"
            } else if (number == 0 && this.yourCart[index].count > 0) {
                this.yourCart[index].count--
                sessionStorage.setItem('yourCart', JSON.stringify(this.yourCart))
                window.location.href = "/giohang"
            }
        },
        updateCart() {
            this.yourCart = JSON.parse(sessionStorage.getItem('yourCart'))
            this.yourCart.forEach((el, index) => {
                if (el.count == 0) {
                    this.yourCart.splice(index, 1)
                    sessionStorage.setItem('yourCart', JSON.stringify(this.yourCart))
                    window.location.href = "/giohang"
                }
            })
        },
        clearItem(index) {
            this.yourCart = JSON.parse(sessionStorage.getItem('yourCart'))
            this.yourCart.splice(index, 1)
            sessionStorage.setItem('yourCart', JSON.stringify(this.yourCart))
            window.location.href = "/giohang"
        },
        back() {
            window.location.href="/home"
        }
    }, created() {
        this.yourCart = JSON.parse(sessionStorage.getItem('yourCart'))
        if (this.yourCart.length == 0) {
                this.listShow = true
            } else {
                this.listShow = false
            }
    }
}
</script>

<style>
.cart {
    min-height: 100vh;
    padding-top: 150px;
}

.cart-th {
    border-bottom: 4px solid rgb(177, 177, 177);
    font-weight: bold;
}

.cart-item {
    margin-top: 10px;
    border-bottom: 1px solid rgb(177, 177, 177);
    padding-bottom: 5px;
}

.cart-item__img {
    display: flex;
    align-items: center;
}

.cart-item__img button {
    margin-right: 10px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    border: 1px solid rgb(140, 139, 139);
}

.cart-item__img button:hover {
    background-color: rgb(177, 177, 177);
}

.cart-item__img img {
    width: 76px;
    margin-right: 10px;
}

.cart-item__price,
.cart-item__count,
.cart-item__total {
    display: flex;
    align-items: center;
}

.cart-item__count button {
    width: 30px;
    height: 30px;
    line-height: 30px;
    border: 1px solid rgb(177, 177, 177);
}

.cart-item__count p {
    width: 30px;
    margin: 0;
    text-align: center;
}
.back,
.cart-update {
    padding: 5px 10px;
    border: none;
    background-color: rgb(48, 181, 178);
    color: #fff;
    margin-right: 20px;
    margin-top: 20px;
}

.back:hover,
.cart-update:hover {
    box-shadow: 0 0 10px 0 rgb(48, 181, 178);
}

.cart-pay {
    background-color: rgb(248, 112, 134);
    padding: 5px 10px;
    border: 1px solid rgb(177, 177, 177);
    color: #fff;
    font-weight: bold;
}
</style>