<template>
    <div class="product">
        <div class="product-img">
            <a href="" class="w-100"><img :src="require(`../assets/products/${product.imgUrl}`)" alt=""
                    class="w-100"></a>
            <button class="add-btn" @click="addToCart(product,$event)">
                <i class="fa-solid fa-bag-shopping"></i>
            </button>
        </div>
        <div class="product-text">
            <div class="product-category">
                {{product.categoryName}}
            </div>
            <div class="product-name">
                <a href="">{{product.name}}</a>
            </div>
            <div class="product-price">
                {{new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND'}).format(product.price)}}
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: 'Product-component',
    props: ['product'],
    computed: {
        ...mapGetters(['cart','plusShow'])
    },
    methods: {
        addToCart(product,e) {
            console.log(e.target)
            let yourCart = JSON.parse(sessionStorage.getItem('yourCart')) || []
            if (yourCart.length == 0) {
                product.count = 1
                yourCart.push(product)
                sessionStorage.setItem('yourCart', JSON.stringify(yourCart))
            } else {
                let index = yourCart.findIndex(function (element) {
                    return (element.id == product.id)
                })

                if (index >= 0) {
                    yourCart[index].count++
                    sessionStorage.setItem('yourCart', JSON.stringify(yourCart))
                } else {
                    product.count = 1
                    yourCart.push(product)
                    sessionStorage.setItem('yourCart', JSON.stringify(yourCart))
                }
            }
        }
    }
}

</script>

<style>
.product {
    margin: 20px 0;
    text-align: center;
}

.product-img {
    position: relative;
}

.product-text {
    background-color: rgb(253, 249, 249);
    padding-top: 20px;
    padding-bottom: 10px;
}

.product .product-category,
.product .product-name,
.product .product-price {
    margin: 5px 0;
}

.product .product-category {
    color: rgb(162, 162, 162);
}

.product .product-name {
    font-weight: bold;
}

.add-btn {
    display: none;
    background-color: transparent;
    border: none;
    color: rgba(53, 174, 249, 1);
    font-size: 30px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    transition: all ease-in 0.2s;
}

.add-btn:hover {
    font-size: 40px;
}

.product:hover .add-btn {
    display: block;
}
</style>